.footer {
    width: 100%;
    padding: 1rem 0;
    background-color: #d2bf55;
}

.footer-container {
    max-width: 1040px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.left {
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.location {
    display: flex;
    margin-bottom: 0.8rem;
}

.location p {
    line-height: 30px;
}

.phone {
    display: flex;
    margin-bottom: 0.8rem;
}

.phone p {
    line-height: 30px;
}

.email {
    display: flex;
    margin-bottom: 0.8rem;
}

.email p {
    line-height: 30px;
}

.email a:hover {
    color: #fbbfca;
}

.right {
    height: 100%;
    max-width: 100%;
    display: grid;
    margin: auto;
}

.github {
  margin-top: 1rem;
  margin-right: 4rem;  
}

.linkedin {
    margin-top: 1rem;  
    margin-right: 4rem; 
  }

.facebook {
    margin-top: 1rem;  
    margin-right: auto; 
  }

.right a:hover {
    color: #fbbfca;
}

@media screen and (max-width: 640px) {
    .footer-container {
        grid-template-columns: 1fr;
    }
}